.header {
  height: auto;
  margin-bottom: 0;
  font-family: Helvetica;
  text-align: center;
  display: run-in;
  border: 1px solid #333;
  background-color: #333;
  height: 825px;
}

.header h1{
  text-shadow: 5px 4px 8px black;
  font-family: 'Montserrat', sans-serif;
  letter-spacing: 10px;
}

.header_img {
  height: 250px;
  margin: auto;
  position: relative;
  display: block;
  margin-bottom: 40px;
}

.header_img_profile {
  height: inherit;
  width: inherit;
  margin: auto;
  margin-top: 2em;
  position: relative;
  /*border: 5px solid #333;*/
  border-radius: 5px 20px 5px;
  border: 2px solid #5d5c61;
  border-radius: 5px 20px 5px;
  box-shadow: 4px 6px 8px black;
}

.header_description {
  background-color: #444444;
  /* opacity: .7; */
  margin-top: 4em;
  letter-spacing: 3px;
}

.name {
  color: whitesmoke;
  font-size: 60px;
  margin-top: 6em;
}

.name h1 {
  letter-spacing: 10px;
}
.contentCard {
  margin: 0;
  font-family: Helvetica;
  font-size: 20px;
  text-align: justify;
  position: relative;
  min-height: 80%;
  height: 350px;
  /* opacity: .95; */
}

.contentCard h2 {
  margin: 0;
  padding-top: 25px;
}
.infoBubble_Container {
  width: 20%;
}

.infoBubble_Head {
  border: 1px solid #f4f4f4;
  border-left: 0;
  border-right: 0;
  padding-top: 0;
  display: block;
  position: relative;
  margin-bottom: 40px;
  margin-top: 40px;
}

.infoBubble_Head h3 {
  margin: 10px;
}

.infoBubble {
  display: flex;
  position: relative;
  justify-content: space-between;
  margin: 40px;
  margin-top: 30px;
  text-align: center;
}

.infoBubble p {
  line-height: 30px;
  word-spacing: 3px;
  font-size: 1.3em;
  letter-spacing: 2px;
  white-space: pre-wrap;
}

.infoBubble img {
  width: 40%;
  position: relative;
  display: block;
  cursor: pointer;
  margin: 0 auto;
  overflow: hidden;
  cursor: pointer;
}

@mixin transition($in) {
	transition:$in;
	-webkit-transition:$in;
	-moz-transition:$in;
	-o-transition:$in;
	-ms-transition:$in;
}

.button {
  outline:none;
	border:none;
  background-color:transparent;
	position:relative;
  @include transition(all 0.5s ease);
}

.button:hover {
  animation: shakeThatBooty 0.3s linear 1;
  -webkit-animation: shakeThatBooty 0.3s linear 1;
  -moz-animation: shakeThatBooty 0.3s linear 1;
  -o-animation: shakeThatBooty 0.3s linear 1;
  -ms-animation: shakeThatBooty 0.3s linear 1;
}

@-webkit-keyframes shakeThatBooty {
  33% {
    transform: rotateZ(10deg);
    -webkit-transform: rotateZ(10deg);
    -moz-transform: rotateZ(10deg);
    -o-transform: rotateZ(10deg);
    -ms-transform: rotateZ(10deg);
  }
  67% {
    transform: rotateZ(-10deg);
    -webkit-transform: rotateZ(-10deg);
    -moz-transform: rotateZ(-10deg);
    -o-transform: rotateZ(-10deg);
    -ms-transform: rotateZ(-10deg);
  }
  100% {
    transform: rotateZ(10deg);
    -webkit-transform: rotateZ(10deg);
    -moz-transform: rotateZ(10deg);
    -o-transform: rotateZ(10deg);
    -ms-transform: rotateZ(10deg);
  }
}

@keyframes shakeThatBooty {
  33% {
    transform: rotateZ(10deg);
    -webkit-transform: rotateZ(10deg);
    -moz-transform: rotateZ(10deg);
    -o-transform: rotateZ(10deg);
    -ms-transform: rotateZ(10deg);
  }
  67% {
    transform: rotateZ(-10deg);
    -webkit-transform: rotateZ(-10deg);
    -moz-transform: rotateZ(-10deg);
    -o-transform: rotateZ(-10deg);
    -ms-transform: rotateZ(-10deg);
  }
  100% {
    transform: rotateZ(10deg);
    -webkit-transform: rotateZ(10deg);
    -moz-transform: rotateZ(10deg);
    -o-transform: rotateZ(10deg);
    -ms-transform: rotateZ(10deg);
  }
}

.infoBubble_Container p {
  font-size: 16px;
}

